.partners {
  z-index: 1;
  position: relative;
  margin-bottom: 60px;

  &__button--toggle {
    padding: 0;
    background: transparent;
    border: none;
    box-shadow: none;
    cursor: pointer;
    color: #ffffff;
    text-decoration: underline;
    font-weight: 600;
  }

  .partners-title {
    padding: 30px 0;
    font-size: 28px;
    text-transform: capitalize;

    @media (max-width: $screen-md-max) {
      text-align: center;
    }
  }

  .partners-list {
    &__item {
      display: flex;
      justify-content: space-between;
      text-align: center;
      min-height: 120px;
      position: relative;
      flex-wrap: wrap;
      align-items: center;
      background-color: #fff;
      border-radius: 5px;
      margin-bottom: 9px;
      color: #000000;
    }

    &__description {
      display: flex;
      text-align: center;
      flex-wrap: wrap;
      align-items: center;
      font-size: 12px;
      padding: 0 4em 10px;
      background-color: transparent;
      justify-content: center;
      border-radius: 5px;
      margin: auto auto 9px;
      width: 100%;

      &:first-of-type {
        display: none;
      }
    }
  }

  .partners-list {
    @media (max-width: $screen-md-max) {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
    }
  }

  .partners-item {
    @media (max-width: $screen-md-max) {
      width: 320px;
      background: linear-gradient(to left, #fff, #d8d8d8 50%, #fff 50%);
      position: relative;
    }

    &__name {
      margin: 18px 0;
      padding: 0 35px;
      width: 200px;
      z-index: 1;
      position: relative;

      @media (max-width: $screen-md-max) {
        width: 150px;
        padding: 0;
        margin: 25px 0 0;
      }

      a {
        display: flex;
        align-items: center;
        flex-direction: column;
        width: 160px;
        height: 100px;

        img {
          width: 160px;
          height: 100px;
        }
      }
    }

    &__bonus {
      width: 240px;
      margin: 15px 0;
      opacity: 1;
      transition: opacity .2s ease-out;
      transition-delay: .3s;
      z-index: 1;
      position: relative;
      
      @media (max-width: $screen-md-max) {
        margin: 0;
        position: absolute;
        top: 10px;
        left: 165px;
        width: auto;
      }

      p {
        margin-bottom: 5px;
        padding-top: 15px;
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        text-transform: uppercase;
        
        @media (max-width: $screen-md-max) {
          padding-top: 0;
          font-size: 10px;
          text-align: left;
          width: 100px;
        }
      }

      h4 {
        color: #000;
        font-size: 18px;
        font-weight: 600;
        line-height: 18px;

        @media (max-width: $screen-md-max) {
          margin-bottom: 6px;
          font-size: 14px;
          line-height: 14px;
          text-align: left;
          width: 100px;
        }
      }
    }

    &__rating {
      display: flex;
      flex-direction: column;
      justify-content: center;
      z-index: 1;
      position: relative;
      width: 120px;
      margin: 15px 0;
      
      @media (max-width: $screen-md-max) {
        margin: 0;
        width: auto;
        position: absolute;
        left: 165px;
        top: 75px;
      }

      p {
        display: block;
        margin-bottom: 5px;
        padding-top: 15px;
        color: #000;
        font-size: 14px;
        line-height: 14px;
        text-align: center;
        text-transform: uppercase;
        
        @media (max-width: $screen-md-max) {
          padding-top: 5px;
          padding-right: 33px;
          font-size: 10px;
          text-align: left;
        }
      }

      a {
        @media (max-width: $screen-md-max) {
          margin-right: -10px;
        }
      }
    }

    &__score {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-self: center;
      width: 100px;
      z-index: 1;
      position: relative;
      margin: 15px 0;

      @media (max-width: $screen-md-max) {
        margin: 0;
        position: absolute;
        left: 165px;
        top: 150px;

      }

      h4 {
        margin: 0 auto;
        font-size: 25px;
        font-weight: 600;
        text-align: center;
        
        @media (max-width: $screen-md-max) {
          margin: 0;
          font-size: 32px;
          line-height: 35px;
          max-width: 44px;
          overflow: hidden;
          white-space: nowrap;
        }
      }

      p {
        @media (max-width: $screen-md-max) {
          display: none;
        }
      }
    }

    &__links {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-self: center;
      width: 225px;
      padding-right: 35px;
      margin: 15px 0;
      z-index: 1;
    }

    &__button {
      cursor: pointer;
      text-decoration: none;
      text-transform: uppercase;
      transition: all .4s ease-out;
      background: transparent;

      &--facts {
        width: 111px;
        margin: 0 auto;
        border: 1px solid #2660a4;
        line-height: 27px;
        display: block;
        padding: 0 8px;
        border-radius: 6px;
        color: #2660a4;
        font-size: 12px;
        
        @media (max-width: $screen-md-max) {
          margin: 10px -5px 0 0;
          display: inline;
          padding: 0;
          border: 0;
          font-size: 12px;
          line-height: 11px;
          text-decoration: underline;
          text-transform: capitalize;
          color: #000000;
          text-align: left;
        }

        &:hover {
          background-color: #2660a4;
          color: #fff;
        }
      }

      &--bonus {
        margin-bottom: 10px;
        padding: 12px 5px;
        border-radius: 25px;
        background-image: linear-gradient(255deg, #dcb74f, #b98518);
        box-shadow: 0 1.5px 2.5px 0 rgba(0, 0, 0, 0.15);
        color: #fff;
        font-size: 18px;
        font-weight: 600;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        
        @media (max-width: $screen-md-max) {
          width: 130px;
          margin-top: 10px;
          margin-left: 15px;
          font-size: 16px;
        }

        &:hover {
          background-image: linear-gradient(25deg, #dcb74f, #b98518);
          color: #fff;
        }

        img {
          width: 22px;

          @media (max-width: $screen-md-max) {
            display: none;
          }
        }
      }
    }

    &__review {
      display: inline-block;
      font-size: 14px;
      color: black;
      font-weight: 500;
      transition: all .4s ease-out;

      @media (max-width: $screen-md-max) {
        position: absolute;
        left: 165px;
        top: 190px;
        font-size: 12px;
        line-height: 11px;
        text-decoration: underline;
        text-transform: capitalize;
      }
    }

    .mod-popular {
      display: inline-block;
      position: absolute;
      top: -10px;
      left: 0;
      height: 20px;
      padding: 3px 8px;
      background: #c60c30;
      color: #fff;
      font-size: 11px;
      text-align: center;
      white-space: nowrap;

      @media (max-width: $screen-md-max) {
        display: none;
      }

      &:after {
        display: block;
        position: absolute;
        top: 0;
        right: -18px;
        border: 10px solid #c60c30;
        border-right-color: transparent;
        content: "";
      }

      span {
        position: relative;
        top: 1px;
        text-transform: uppercase;
      }
    }
  }

  .partners-description {
    text-decoration: none;
    color: #fff;
    cursor: pointer;
    transition: all .4s ease-out;

    &__link {
      display: flex;
      
      @media (max-width: $screen-md-max) {
        max-width: 320px;
        margin: auto;
        padding: 0 48px;
        display: block;
      }
    }

    &__initital {
      display: block;

      &.hide {
        display: none;
      }
    }

    &__additional {
      display: block;

      &.hide {
        display: none;
      }
    }

    a {
      text-decoration: none;
      color: #fff;
      cursor: pointer;
      transition: all .4s ease-out;
    }
  }

  .partners-slots {
    z-index: 1;
    position: relative;
    display: flex;
    justify-content: center;
    gap: 0 20px;

    @media (max-width: $screen-md-max) {
      flex-wrap: wrap;
      gap: 30px;
    }

    &__item {
      background-color: #fff;
      box-shadow: 0 4px 17px 0 rgba(0, 0, 0, 0.15);
      color: #000;

      @media (max-width: $screen-md-max) {
        width: 220px;
      }
    }

    &__link {
      padding: 10px 10px 0;
      display: flex;

      img {
        width: 245px;
        height: 146px;
      }
    }

    &__title {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 0 10px;
      font-size: 18px;
      height: 50px;
      text-align: center;
    }

    .slots-list {
      &__item {
        display: flex;
        align-items: center;
        padding: 0 10px;
        border-top: 1px solid #e9e9e9;
        height: 39px;
        justify-content: space-between;

        &:first-child img {
          width: 48px;
          height: 35px;
        }

        span {
          font-weight: 600;
        }
      }
    }

    .partners-item__button {
      margin: 0 10px 10px;
    }
  }
}