.top-filters {
  padding: 15px 0;

  .features-list {
    display: flex;
    align-items: center;
    gap: 0 20px;
    
    @media (max-width: $screen-md-max) {
      flex-wrap: wrap;
      justify-content: center;
      gap: 10px 20px;
      margin-bottom: 15px;
    }

    li {
      display: flex;
      align-items: center;
      gap: 0 5px;

      span {
        font-size: 12px;
        font-weight: 100;
        text-transform: uppercase;
      }
    }
  }
}