.footer {
  padding: 40px 0;
  background-color: #0a0b0b;
  color: #ffffff;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 30px;
    
    @media (max-width: $screen-md-max) {
      flex-direction: column;
      gap: 20px 0;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 14px;
    font-weight: 100;
    letter-spacing: .4px;

    @media (max-width: $screen-md-max) {
      flex-direction: column-reverse;
      gap: 20px 0;
    }
  }

  &__partners {
    display: flex;
    gap: 0 10px;
    
    @media (max-width: $screen-sm-max) {
      flex-direction: column;
      gap: 5px 0;align-items: center;
    }
  }

  &__menu {
    &--list {
      display: flex;
      gap: 0 20px;

      @media (max-width: $screen-md-max) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 5px 0;
      }
    }
  }

  .copyright {
    font-size: 12px;
    margin-top: 10px;
  }

  .scroll-top {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    height: 50px;
    width: 50px;
    right: 2rem;
    bottom: 2rem;
    border-radius: 100%;
    background: linear-gradient(180deg, #05D86A 0%, #047E29 100%);
    border: none;
    cursor: pointer;
    opacity: 100%;
    transition: background .4s, opacity .5s, visibility .5s;
    transform: rotate(90deg);
    z-index: 999;

    &:hover {
      background: #2aaff7;
    }

    &.hidden {
      opacity: 0;
    }

    span {
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 50px;
      line-height: 50px;
      width: 50px;
      height: 50px;
      margin-top: -9px;
      color: #ffffff;
    }
  }
}