.bottom-content {
  background: #ffffff;
  z-index: 1;
  position: relative;
  padding: 30px 10px;
  color: #000;

  .content-short {
    margin-bottom: 15px;
  }

  .bottom-content-title {
    margin-bottom: 15px;
    font-size: 16px;
    font-weight: 700;
  }

  .content-short {
    margin-bottom: 15px;
  }

  .content-long {
    display: none;

    &.show {
      display: block;
    }
  }

  .content-more {
    display: flex;
    position: relative;
    margin-top: 10px;
    padding: 0 32px 0 16px;
    border: 1px solid #eceaea;
    border-radius: 6px;
    background-color: #000;
    color: #fff;
    line-height: 30px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;

    &.show {
      &:after {
        transform: rotate(-180deg);
      }
    }

    &:after {
      position: absolute;
      top: 0;
      right: 0;
      width: 30px;
      height: 30px;
      background-image: url(../img/chevron.svg);
      background-position: center;
      background-repeat: no-repeat;
      background-size: 12px;
      content: '';
    }
  }

  p {
    margin-bottom: 16px;
  }

  ul {
    padding-left: 37px;

    li {
      margin-bottom: 16px;
    }
  }
}