.header {
  background-color: #0a0b0b;

  &__inner {
    display: flex;
    align-items: center;
    min-height: 60px;
    padding: 12.5px 0;
    justify-content: space-between;
  }

  &__logo {
    a {
      display: flex;
      width: 100%;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      transition: all .4s ease-out;
    }

    img {
      width: 170px;
      height: 28px;
    }
  }

  &__nav {
    ul {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      gap: 0 20px;
      margin-bottom: 0;
      list-style: none;
      transition: all .4s ease-out;

      @media (max-width: $screen-md-max) {
        display: none;
      }

      li {
        a {
          position: relative;
          color: #fff;
          text-decoration: none;
          transition: all .4s ease-out;

          @media (max-width: $screen-lg-max) {
            font-size: 14px;
          }

          &:hover {
            background-color: #BD0028;
            padding: 4px 30px;
            border-radius: 6px;
            transition: .4s ease;
          }
        }

        &.active {
          a {
            font-weight: 700;
          }
        }
      }
    }
  }

  .header-burger {
    display: flex;
    z-index: 11;
    position: absolute;
    top: 50%;
    right: 14px;
    align-items: center;
    justify-content: center;
    width: 38px;
    height: 32px;
    margin-top: -16px;
    background-color: transparent;
    cursor: pointer;

    @media (min-width: $screen-lg-min) {
      display: none;
    }

    span {
      display: block;
      position: absolute;
      width: 18px;
      height: 2px;
      border-radius: 1px;
      background-color: #fff;
      top: 50%;
      margin-top: -1px;

      &:before {
        top: -6px;
        content: "";
        display: block;
        position: absolute;
        width: 18px;
        height: 2px;
        border-radius: 1px;
        background-color: #fff;
      }

      &:after {
        bottom: -6px;
        content: "";
        display: block;
        position: absolute;
        width: 18px;
        height: 2px;
        border-radius: 1px;
        background-color: #fff;
      }
    }
  }
}