.top-content {
  padding: 35px 0 0;
  z-index: 1;
  position: relative;

  @media (max-width: $screen-md-max) {
    text-align: center;
    margin-bottom: 50px;
  }

  &__inner {
    display: flex;
    gap: 0 65px;
    justify-content: space-between;
  }

  &__text {
    width: 100%;
  }
  
  &__descr {
    @media (max-width: $screen-md-max) {
      max-width: 620px;
      margin: 0 auto;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }

    @media (max-width: $screen-sm-max) {
      font-size: 14px;
    }
  }

  &__logo {
    width: 115px;
    flex-shrink: 0;
    font-size: 12px;
    line-height: 1;
    text-align: center;
    text-transform: uppercase;
    
    @media (max-width: $screen-md-max) {
      display: none;
    }
  }

  &__title {
    font-size: 28px;
    text-transform: capitalize;
    margin-bottom: 14px;
    
    @media (max-width: $screen-sm-max) {
      font-size: 20px;
    }
  }

  &__license {
    margin-bottom: 5px;
  }
}